<template>
  <div>

    <div class="container scontainer" >
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
      <Empty v-if=" list.length == 0"></Empty>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="list"
        v-else
      >
        <div v-for="(item, index) in list" class="item" :key="index">
          <div class="info">
            <div class="title">
              货号{{item.number}}
              <span style="padding-left: 20px;">品名{{item.title}}</span>
            </div>
            <div class="tips">型号:{{item.model == '' ? '暂无': item.model}}</div>
            <div class="tips">产品属性:{{item.attribute == '' ? '暂无': item.attribute}}</div>
            <div class="tips">
              规格:{{item.standards}}
              <span style="padding-left: 20px;">单位:{{item.unit}}</span>
            </div>
             <div class="tips">{{item.explanfield ? item.explanfield :''}}</div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { product } from "@api/public";
import { Icon, Search, Loading, List } from "vant";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "Product",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings,
  },

  data: function () {
    return {
      list: [],
      keywords: "",
      page: "",
      loading: false,
      finished: false,
    };
  },
  created() {},
  mounted: function () {
    
  },
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
      }
 
    },

    getData() {
      product({
        search: this.keywords,
        page: this.page,
        length: 30,
      }).then((res) => {
        if (this.page != "") {
          this.list = this.list.concat(res.data.product);
        } else {
          this.list = res.data.product;
        }

        this.finished = res.data.product.length < 30 ? true : false;
        this.loading = res.data.product.length < 30 ? true : false;
        if (res.data.product.length == 30) {
          this.page = this.list[this.list.length - 1].pid;
        }
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.container {
  background: #fff;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  display: block;
  padding: 15px 8px;

  border-bottom: 10px solid #f9f9f9;
}

.container .list .item div {
  width: 100%;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}
.container .list .item .tips{
  color: #999;
}
.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.company {
  color: #333;
  font-size: 16px;
}

.contact {
  color: #999;
  margin: 5px 0;
  font-size: 12px;
}

.contact span {
  padding-left: 10px;
}

.address {
  color: #999;
  font-size: 12px;
  margin-bottom: 5px;
}

.list img {
  width: 30px;
}

.loading-tips {
  text-align: center;
  padding-top: 150px;
}
.info .price {
  font-size: 12px;
  color: #999;
}
.container .list .item .title{
  font-size: 14px;
}
</style>
